import { Component, ViewChild } from '@angular/core';
import { Nav, Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
  import { ApiProvider } from '../providers/api/api';
  import { ModalController } from 'ionic-angular';

import { Events } from 'ionic-angular';

import { HomePage } from '../pages/home/home';
import { ListPage } from '../pages/list/list';


@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;

  rootPage: any = 'InicioPage';

  pages: Array<{title: string, component: any}>;

  constructor(public platform: Platform, public statusBar: StatusBar, public splashScreen: SplashScreen, public apiProvider: ApiProvider, public events: Events, public modalCtrl : ModalController) {
    this.initializeApp();

     events.subscribe('userCreated', (user) => {
     (<any>this).userDataProfile = user;
  });

  events.subscribe('userLogout', () => {
      this.userDataProfile = false;
      //console.log(user);
    });

    
  }


     CalculateAge(fecha)
     {
     console.log(fecha);
         if(fecha){
            var timeDiff = Math.abs(Date.now() - new Date(fecha));
            //Used Math.floor instead of Math.ceil
            //so 26 years and 140 days would be considered as 26, not 27.
            var edadc = Math.floor((timeDiff / (1000 * 3600 * 24))/365);
            console.log(edadc);
            return edadc;
        }
    }


  initializeApp() {
    this.platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.

      this.statusBar.styleDefault();
      this.splashScreen.hide();

      this.apiProvider.verificarLogin()
    .then(data => {    
      console.log(data);
      this.userDataProfile = data;
     });



    });
  }



 


  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.nav.setRoot(page.component);
  }

  goPerfil() {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.nav.setRoot('PerfilPage');
  }
  goHorarios() {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
   // this.nav.setRoot(HomePage);
   this.nav.setRoot('HorariosPage');
  }

    goHome() {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
   // this.nav.setRoot(HomePage);
   this.nav.setRoot('InicioPage');
  }


      openModalLogin() {
    let modalPage = this.modalCtrl.create('LoginPage');
    modalPage.present();
      }


goRutina() {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
   // this.nav.setRoot(HomePage);

   if(this.userDataProfile){
    this.nav.setRoot('MiRutinaPage');
   }
   else{
   this.openModalLogin(); 
   }
   
  }

goReservar() {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
   // this.nav.setRoot(HomePage);
   this.nav.setRoot('ReservarPage');
  }


goEstadistica() {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
   // this.nav.setRoot(HomePage);

   if(this.userDataProfile){
    this.nav.setRoot('EstadisticaPage');
   }
   else{
   this.openModalLogin(); 
   }
   
  }

goNovedades() {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
   // this.nav.setRoot(HomePage);
   this.nav.setRoot('NovedadesPage');
  }


  goEquipamento() {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
   // this.nav.setRoot(HomePage);
   this.nav.setRoot('EquipamentoPage');
  }

  goQR() {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
   // this.nav.setRoot(HomePage);
   this.nav.setRoot('EscanerPage');
  }



}



